.contact-container {
  height: 100%;
}

#contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
}

#contact-form h3 {
  font-size: 1.8em;
  margin-bottom: 60px;
  background-color: #040c58;
}

#contact-form form {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 50%;
  margin-bottom: 6%;
}

#contact-form form input,
#contact-form form textarea {
  margin: 10px 0;

  background-color: #040c58;
  border: none;
  border-bottom: 1px solid white;
  border-radius: 3px 3px 0 0;

  font-family: inherit;
  font-size: 1.35em;
  color: white;

  transition: background-color 0.3s;
}

#contact-form form input:focus,
#contact-form form textarea:focus {
  border-radius: 3px;
  color: black;
  background-color: white;
}

#contact-form form input::placeholder,
#contact-form form textarea::placeholder {
  color: inherit;
  opacity: 0.5;
}

#recaptcha-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

#contact-form form button {
  font-family: inherit;
  font-size: 1.5em;
  color: white;

  margin-top: 35px;

  border: none;
  background-color: #1d399c;

  cursor: pointer;

  transition: background-color 0.3s;
}

#contact-form form button:hover {
  background-color: white;
  color: #1d399c;
}

@media screen and (max-width: 800px) {
  .particles {
    visibility: hidden;
  }

  #contact-form {
    height: auto;
    height: auto;
  }

  #contact-form form {
    height: auto;
    width: auto;
  }

  #rc-imageselect, .g-recaptcha {
    transform:scale(0.6);
    -webkit-transform:scale(0.6);
    transform-origin:0 0;
    -webkit-transform-origin:0 0;
  }
}

#g-recaptcha-response-1 {
  display: none;
}